import "../App.css";

function Student(props) {
  let imgCode = props.kod;
  let studentCount = 1 + props.szam;
  return (
    <div className={`s${studentCount} common`}>
      <img
        className="studentimg"
        src={`img/${imgCode}.jpg`}
        alt={`img/${imgCode}.jpg`}
      ></img>
      <p className="name">{props.nev}</p>
    </div>
  );
}

export default Student;
